.TodoSearch-input {
    background: #F9FBFC;
    border-radius: 10px;
    border: 2px solid #202329;
    /* margin: 0 24px; */
    height: 64px;
    width: 100%;
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch-input::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSearch-input:focus {
    outline-color: #61DAFA;
}