body {
  margin: 0;
  padding: 0;
  background: #F2F4F5;
  color: #1E1E1F;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  /* min-height: 100vh; */
}

.TodoCounter-title{
    font-size: 24px;
    text-align: center;
    margin: 30px auto;
    padding: 30px;
    border-bottom: 1px solid;
}
.TodoSearch-input {
    background: #F9FBFC;
    border-radius: 10px;
    border: 2px solid #202329;
    /* margin: 0 24px; */
    height: 64px;
    width: 100%;
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch-input::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSearch-input::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSearch-input:focus {
    outline-color: #61DAFA;
}
ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
}
.TodoItem {
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
    border: solid 1px lightgray;
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 50px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
}

.Icon svg {
    width: 30px;
    height: 30px;
}
 
.Icon-check {
    position: absolute;
    left: 12px;
}

.Icon-check--active {
    color: #4caf50;
}

.Icon-delete {
    position: absolute;
    right: 0;
}

.Icon-delete:hover {
    color: red;
}
form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
}
.TodoForm-spanError {
    display: none;
}
.TodoForm-spanError--active {
    display: block;
    color: red;
}

.TodoForm-textArea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
    resize: none;
}

.TodoForm-textArea--error{
    border: 1px solid red;
}

textarea::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

textarea:focus {
    outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
}

.TodoForm-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    transition: 0.2s;
    border-radius: 10px;
}
.TodoForm-button--add:hover {
    background-color: rgb(144, 238, 144);
    box-shadow: 0px 5px 25px rgba(144, 238, 144, 0.5);
    transition: 0.2s;
}

.TodoForm-button--cancel {
    background: transparent;
    transition: 0.2s;
    border-radius: 10px;
}
.TodoForm-button--cancel:hover {
    background-color: rgb(211, 211, 211);
    box-shadow: 0px 5px 25px rgba(211, 211, 211, 05);
    transition: 0.2s;
}
.CreateTodoButton {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    /* transform: rotate(0deg); */
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: .3s ease;
    padding: 0;
    margin: 0;
    z-index: 100;
}
.CreateTodoButton:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    /* transform: rotate(226deg); */
    background-color: rgb(144, 238, 144);
    box-shadow: 0px 5px 25px rgba(144, 238, 144, 0.5);
}

.iconButton{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.iconButton svg{
    width: 30px;
    height: 30px;
}
.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
.TodoItem__loading{
    width: 100%;
    height: 70px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 10px;
    border: solid 1px lightgray;
    background-color: #FAFAFA;
    background: linear-gradient(110deg, #FAFAFA 8%, lightgray 18%, #FAFAFA  33%);
    background-size: 200% 100%;
    -webkit-animation: 1.5s shine linear infinite;
            animation: 1.5s shine linear infinite;
}

@-webkit-keyframes shine {
    to {
      background-position-x: -200%;
    }
  }

@keyframes shine {
    to {
      background-position-x: -200%;
    }
  }
  
